import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import { useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import { Competence, Organisation } from "../../../../domain/domain";
import SearchIcon from "../../../../ui-components/icons/Search.icon";
import Input from "../../../../ui-components/input/input";
import { searchOrg } from "../../api";
import { StepsPropsType } from "./types";
import architecture from '../../../../asset/svg/architecture.svg';

const JoinOrgScreen = ({ onChange, data, errors }: StepsPropsType) => {
    const { t } = useTranslation();
    const [query, setQuery] = useState('');
    const [options, setOptions] = useState<Organisation[]>([]);
    const [isLoading, setIsLoading] = useState(false);
    const handleSearchForOrganization = (e:any) => {
        e?.stopPropagation();
        if ((e?.key && e?.key?.toLowerCase() === 'enter' || !e?.key) && query) {
            setIsLoading(true);
            searchOrg(query).then((resp) => {
                setIsLoading(false);
                setOptions(resp);
                console.log(resp);
            });
        }
    };
    return (
        <div>
            <Input
                type="text"
                title={t('find-your-organisation')}
                onChange={(e:any) => setQuery(e?.target?.value)}
                onKeyDown={handleSearchForOrganization}
                icon={<SearchIcon background="#959595" style={{ transform: 'scale(1.4)', marginRight: 15 }} onClick={handleSearchForOrganization} />}
            />
            {
                options.length > 0 ? (
                    options.map((org: Organisation) => (
                        <div style={{ display: 'flex', alignItems: 'center', }} key={org.id}>
                            <img alt={org.name ?? ''} src={org.picture ?? architecture} />
                            <div>{org.name}</div>
                        </div>
                    ))
                ) : null
            }
        </div>
    )
}

export default JoinOrgScreen;
