import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useDispatch, useSelector } from "../../../hooks";
import { paths } from "../../../routes";
import { logIn } from "../../../services/redux/reducers/auth/auth.slice";
import Input from "../../../ui-components/input/input";
import ShowHide from "../../../ui-components/input/ShowHide.component";
import SelectOrgScreen from "./SelectOrg.screen";

const LoginScreen = () => {
    const { t } = useTranslation();
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [emailError, setEmailError] = useState(false);
    const [pwdError, setPwdError] = useState(false);
    const [emailErrorMsg, setEmailErrorMsg] = useState<string>(t('invalid-email'));
    const [pwdErrorMsg, setPwdErrorMsg] = useState<string>(t('invalid-password'));
    const dispatch = useDispatch();
    const [params, setParams] = useSearchParams();
    const navigate = useNavigate();
    const step = params.get('step');
    const {payload} = useSelector((state) => state.user);
    const [showPassword, setShowPassword] = useState(false);

    useEffect(() => {
        const {code, content, errors} = payload;
        if (errors.length <= 0 && content.access_token.length > 0) {
            localStorage.setItem('access_token', content.access_token);
            setParams({ step: 'select_org' });
        }
    }, [payload]);
    const checkForm = () => {
        let email_regex = new RegExp('[a-z0-9]+@[a-z]+\.[a-z]{2,3}');
        let pwd_regex = new RegExp('(?=.{8,})(?=.*[^A-Za-z0-9])')
        if (!email_regex.test(email) || !pwd_regex.test(password)) {
            if (!email_regex.test(email)) {
                setEmailError(true);
            }
            if (!pwd_regex.test(password)) {
                setPwdError(true);
            }
            return false;
        }
        return true;
    }

    const onSubmit = () => {
        if (checkForm()) {
            dispatch(logIn({ username:email, password }));
        }
    };
    const onSubmitOrg = () => {
        navigate(paths.DASHBOARD);
    }
    return (
        <div id="login" className="sign_form-container auth-content-cc">
            {
                step === 'select_org' ? (
                    <SelectOrgScreen onSubmitOrg={onSubmitOrg} />
                ) : (
                    <>
                        { payload.errors.length > 0  && <span className={"errorMsg"}>{t('invalid-login')}</span>}
                        <Input type={"text"} title={t('email')} doThings={setEmail} hasError={emailError} errorMsg={emailErrorMsg} />
                        <Input
                            type={showPassword ? 'text' : 'password'}
                            title={t('password')}
                            doThings={setPassword}
                            hasError={pwdError}
                            errorMsg={pwdErrorMsg}
                            icon={
                                <ShowHide onClick={() => setShowPassword(prev => !prev)} show={showPassword} />
                            }
                        />
                        <Input type={"submit"} title={t('continue')} doThings={onSubmit} iconName={faAngleRight}/>
                    </>
                )
            }
        </div>
    )
};
export default LoginScreen;
