import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import { useCallback, useState } from "react";
import { AsyncTypeahead } from "react-bootstrap-typeahead";
import { useTranslation } from "react-i18next";
import { Competence } from "../../../domain/domain";
import Input from "../../../ui-components/input/input";
import { searchOrg } from "../api";

const SelectOrgScreen = ({ onSubmitOrg }: { onSubmitOrg: () => void}) => {
    const { t } = useTranslation();
    const [query, setQuery] = useState('');
    const [option, setOptions] = useState<Competence[]>([]);
    const [value, setValue] = useState<any>([]);
    const [isLoading, setIsLoading] = useState(false);
    const handleSearchForOrganization = useCallback((q: string) => {
        setIsLoading(true);
        searchOrg(q).then((resp) => {
            setIsLoading(false);
            setOptions(resp);
        });
    }, []);
    const handleSubmitOrg = () => {
        if (value.length < 1) {
            console.log(query)
        } else {
            console.log(value)
            onSubmitOrg();
        }
    }
    return (
        <>
            <AsyncTypeahead id="rome"
                className="auth-input-typeahead"
                onInputChange={(e) => {setQuery(e)}}
                onChange={(e) => setValue(e)}
                onSearch={handleSearchForOrganization}
                options={option} labelKey={"name"}
                minLength={1}
                isLoading={isLoading}
                useCache={false}
                renderMenuItemChildren={(options: any) => <span>{options.name}</span>}
                renderInput={({ inputRef, referenceElementRef, ...inputProps }) => (
                    <Input
                      {...inputProps}
                      ref={(input:any) => {
                        inputRef(input);
                        referenceElementRef(input);
                      }}
                      title={t('find-your-organisation')}
                    />
                  )}
            />
            <Input type={"submit"} title={t('continue')} doThings={handleSubmitOrg} iconName={faAngleRight}/>
        </>
    )
}

export default SelectOrgScreen;
